import Elms from "../elements/";

function template_word(wordParam, firstChild, item, i) {
  return '' +
  `<div class="media">
    <figure class="media-left">
      <p class="image word">
        <img src="${ wordParam.path + wordParam.dir + '/' + item[3] }.jpg">
      </p>
    </figure>
    <div class="media-content">
      <div class="content">
        <p>
          <strong>${ firstChild[0] }</strong> <span>${ firstChild[1] }</span> <small>${ firstChild[2] || '' }</small>
        </p>
        <p>${ item[1] }</p>
        <p>${ item[2] }</p>
      </div>
    </div>
  </div>`;
}

function template_list(template) {
  return `<div id="words"><div class="words">${ template }</div></div>`;
}

export function build_wordlist(wordlist, wordParam) {
  const elmWord = Elms.content.word;
  const template = wordlist.map((item, i)=> template_word(wordParam, item[0].split(' '), item, i)).join('');
  elmWord.innerHTML = template_list(template);
}
