import event_playlist from "../event/elements/playlist"
import { build_playlist } from "../elements/playlist";
import { sync_volume } from "../elements/control";
import { load } from "./";
import State from "./state";

export function load_default(index) {
  const playlist = State.playlist.value;
  const current = index === undefined ? State.playlist.index : index;
  const item = playlist[current];

  load(item);
}

export async function init_playlist(url) {
  const response = await fetch(url);
  const json = await response.json();
  State.playlist.value = json.playlist;
  build_playlist(json.playlist);
  event_playlist();
  load_default();
}

export function shuffle() {

}

export function next() {
  const current = State.playlist.index;
  const array = State.playlist.value;
  const length = array.length;
  const shuffle = State.playlist.shuffle;
  const isShuffle = shuffle.length;

  if (isShuffle) {
    const index = shuffle.indexOf(current);
    const next = index + 1;
    const i = next > length - 1 ? 0 : next;
    return shuffle[i];
  } else {
    const next = current + 1;
    const i = next > length - 1 ? 0 : next;
    return i;
  }
}

export function prev() {
  const current = State.playlist.index;
  const array = State.playlist.value;
  const length = array.length;
  const shuffle = State.playlist.shuffle;
  const isShuffle = shuffle.length;

  if (isShuffle) {
    const index = shuffle.indexOf(current);
    const prev = index - 1;
    const i = prev > - 1 ? prev : length - 1;
    return shuffle[i];
  } else {
    const prev = current - 1;
    const i = prev > - 1 ? prev : length - 1;
    return i;
  }
}

export function is_last() {
  const current = State.playlist.index;
  const array = State.playlist.value;
  const length = array.length;
  const shuffle = State.playlist.shuffle;
  const isShuffle = shuffle.length;

  if (isShuffle) {
    const index = shuffle.indexOf(current);
    return index === length - 1;
  } else {
    return current === length - 1;
  }
}

export function is_first() {
  const current = State.playlist.index;
  const array = State.playlist.value;
  const length = array.length;
  const shuffle = State.playlist.shuffle;
  const isShuffle = shuffle.length;

  if (isShuffle) {
    const index = shuffle.indexOf(current);
    return index === 0;
  } else {
    return current === 0;
  }
}

