import { sync_area_time } from "../fn/sync";
import { audio, play } from "../../audio/";
import Elms from "../../elements/";
import State from "../../audio/state";

function play_at_here(line, isLoop) {
  const subtitle = State.audio.subtitle.list;
  const index = parseInt(line.dataset.i);
  const elmEssay = Elms.content.essay;
  const item = subtitle[index];
  const startTime = item.startTime;

  if (State.control.loop.part.flag) {
    State.control.loop.part.flag = false;
    State.control.loop.part.start = -1;
    State.control.loop.part.end = -1;
  }

  if (isLoop) {
    const elmIconLoop = line.querySelector('.action .loop-this');
    if (elmIconLoop.classList.contains('on')) {
      elmIconLoop.classList.remove('on');
      return;
    } else {
      const endTime = item.endTime;
      State.control.loop.part.flag = true;
      State.control.loop.part.start = startTime;
      State.control.loop.part.end = endTime;
    }
  }

  const elmPlay = elmEssay.querySelectorAll('.play-this');
  const elmLoop = elmEssay.querySelectorAll('.loop-this');
  const elmLine = elmEssay.querySelectorAll('.line');
  for (const elm of elmPlay) elm.classList.remove('on');
  for (const elm of elmLoop) elm.classList.remove('on');
  for (const elm of elmLine) elm.classList.remove('current');
  line.classList.add('current');

  if (isLoop) {
    const elmIconLoop = line.querySelector('.action .loop-this');
    elmIconLoop.classList.add('on');
  } else {
    const elmIconPlay = line.querySelector('.action .play-this');
    elmIconPlay.classList.add('on');
    State.audio.subtitle.click = true;
  }

  audio.currentTime = startTime / 1000;
  sync_area_time(startTime);
  if (!State.control.playing) {
    play();
  }
}

export default function() {
  const elmEssay = Elms.content.essay;
  const elmLines = elmEssay.querySelectorAll('.line');

  for (const line of elmLines) {
    const elmIconPlay = line.querySelector('.action .play-this');
    const elmIconLoop = line.querySelector('.action .loop-this');

    elmIconPlay.addEventListener('click', function() {
      play_at_here(line);
    });
    elmIconLoop.addEventListener('click', function() {
      play_at_here(line, 'loop')
    });
  }
}
