import { get_elm } from "../tools/elm";
import { current } from "../audio/subtitle";
import State from "../audio/state";
import Elms from "./";

export function sync_subtitle(currentTime) {
  const loop = State.control.loop.part.flag;
  if (loop) {
    return;
  }

  const elmEssay = Elms.content.essay;
  if (!elmEssay) {
    return;
  }

  const lines = State.audio.subtitle.list;
  if (!(lines && lines.length)) {
    return;
  }

  currentTime = currentTime * 1000;
  for (let i=0; i<lines.length; i++) {
    if (currentTime > lines[i].startTime && currentTime < lines[i].endTime) {
      current(i);
      break;
    }
  }
}

export function sync_current(index) {
  if (index === -1) return;

  const click = State.audio.subtitle.click;
  if (click) {
    State.audio.subtitle.click = false;
    return;
  }

  const elmEssay = Elms.content.essay;
  const elmLines = elmEssay.querySelectorAll('.line');
  const lines = State.audio.subtitle.list;
  const line = lines[index];
  const i = parseInt(line.index) - 1;
  const elmCurrent = elmEssay.querySelector('.line[data-i="' + i + '"]');

  const elmPlay = elmEssay.querySelectorAll('.play-this');
  elmPlay.forEach(elm => elm.classList.remove('on'));

  elmLines.forEach(elm => elm.classList.remove('current'));
  elmCurrent.classList.add('current');

  //const top = elmCurrent.offsetTop;
  //const viewHeight = elmEssay.clientHeight;
  //elmEssay.scrollTo({
  //  behavior: 'smooth',
  //  left: 0,
  //  top: top - viewHeight/2
  //});

  console.log(line);
}

function template_line(line, i) {
  return '' +
  `<div class="line" data-i="${ i }">
    <div class="action">
      <div><div class="play-this"></div></div>
      <div><div class="loop-this"></div></div>
    </div>
    <div class="content">
      ${ line.text }
    </div>
    <div class="audio-duration">
      <span>${ Math.round(((line.endTime - line.startTime) / 1000) * 10) / 10 }</span>
    </div>
  </div>`
}

function template_essay(template) {
  return `<div id="essay"><div class="lines">${ template }</div></div>`;
}

export function build_subtitle(lines) {
  const elmEssay = Elms.content.essay;
  const template = lines.map((line, i) => template_line(line, i)).join('');
  elmEssay.innerHTML = template_essay(template);
}

