import { get_elm } from "../tools/elm";
import Elms from "./";

export function init_progress() {
  Elms.progress.buffered = get_elm('buffered-progress');
  Elms.progress.played = get_elm('played-progress');
  Elms.progress.track = get_elm('progress-slider');
}

export function sync_progress_buffered(buffered, duration) {
  const elmBuffered = Elms.progress.buffered;
  elmBuffered.value = parseFloat(buffered / duration);
}

export function sync_progress_played(current, duration) {
  const elmPlayed = Elms.progress.played;
  const elmTrack = Elms.progress.track;
  const max = elmPlayed.max;
  const value = (current / duration) * max;
  elmPlayed.value = value;
  elmTrack.value = value * 100;
}
