import { get_elm } from "../tools/elm";
import Elms from "./";

export function init_content() {
  const rootElm = Elms.content.root;
  Elms.content.sortlist = get_elm('sortlist', rootElm);
  Elms.content.playlist = get_elm('playlist', rootElm);
  Elms.content.essay = get_elm('essay', rootElm);
  Elms.content.word = get_elm('words', rootElm);
  Elms.content.story = get_elm('story', rootElm);
}
