import { get_elm } from "../tools/elm";
import Elms from "./";

export function init_meta() {
  Elms.meta.root = get_elm('meta-container');
}

export function sync_meta(item) {
  const elmMeta = Elms.meta.root;
  const elmName = get_elm('audio-name', elmMeta);
  const elmArtist = get_elm('audio-artist', elmMeta);
  const elmAlbum = get_elm('audio-album', elmMeta);
  elmName.innerText = item.title || '';
  elmArtist.innerText = item.artist || '';
  elmAlbum.innerText = item.album || '';
}
