import State from "../../audio/state";
import Elms from "../../elements/";

export default function() {
  const elmContent = Elms.content.root;
  const elmTabs = elmContent.querySelectorAll('.tabs li');

  elmTabs.forEach(elm => {
    elm.addEventListener('click', function() {
      if (this.classList.contains('on')) {
        return;
      }
      const index = this.dataset.index;
      const elmDetails = elmContent.querySelectorAll('.frame .roll');
      const elmDetail = elmDetails[index];
      elmTabs.forEach(e => e.classList.remove('on'));
      this.classList.add('on');
      elmDetails.forEach(e => e.classList.add('hide'));
      elmDetail.classList.remove('hide');
    });
  });
}
