import Elms from "../elements/";
import event_sortlist from "../event/elements/sortlist"

function template_item(item, i) {
  return '' +
  `<div class="level_1" data-path="${ item.path }">
    <a href="#">${ item.name }</a>
  </div>`;
}

function template_sort(sort, i) {
  const template = sort.list.map((v, i) => template_item(v, i)).join('');
  return '' +
  `<div class="level_2">
    <div class="level_1">
      <a href="javascript:;">${ sort.category }</a>
    </div>
    ${ template }
  </div>`;
}

function template_list(template) {
  return `<div id="sortlist">${ template }</div>`;
}

export function build_sortlist(sortlist) {
  const elmList = Elms.content.sortlist;
  const template = sortlist.map((sort, i)=> template_sort(sort, i)).join('');
  elmList.innerHTML = template_list(template);
  event_sortlist();
}
