import { init_playlist } from "../../audio/playlist";
import Elms from "../../elements/";

export default function() {
  const elmContent = Elms.content.root;
  const elmTabs = elmContent.querySelectorAll('.tabs li');
  const elmList = Elms.content.sortlist;
  const elmLines = elmList.querySelectorAll('.level_1:not(:first-child)');
  elmLines.forEach(elmItem => {
    elmItem.addEventListener('click', async function(e) {
      e.preventDefault();
      if (this.classList.contains('on')) return;

      const path = this.dataset.path;
      await init_playlist(path);
      elmLines.forEach(v => v.classList.remove('on'));
      this.classList.add('on');
      elmTabs.item(1).click();
    })
  });
  elmLines.item(0).click();
}
