import { play, pause, load } from "../../audio/"
import State from "../../audio/state";
import Elms from "../../elements/";

export default function() {
  const elmList = Elms.content.playlist;
  const elmLines = elmList.querySelectorAll('.line');
  const playlist = State.playlist.value;
  for (const line of elmLines) {
    const index = parseInt(line.dataset.i);
    const elmPlay = line.querySelector('.action > div > div');
    const elmLoop = line.querySelector('.action .loop-it');
    elmPlay.addEventListener('click', function() {
      const playing = State.control.playing;
      const current = State.playlist.index;
      if (index === current) {
        if (playing) {
          pause();
        } else {
          play();
        }
      } else {
        const item = playlist[index];
        load(item);
        State.playlist.index = index;
        play();
      }
    });
    elmLoop.addEventListener('click', function() {
      const looping = State.control.loop.whole.flag;
      const current = State.playlist.index;
      const playing = State.control.playing;
      if (index === current) {
        if (!playing) {
          play();
        }
        if (looping) {
          this.classList.remove('on');
          State.control.loop.whole.flag = false;
        } else {
          this.classList.add('on');
          State.control.loop.whole.flag = true;
        }
      } else {
        const item = playlist[index];
        load(item);
        State.playlist.index = index;
        this.classList.add('on');
        State.control.loop.whole.flag = true;
        play();
      }
    });
  }
}
