import { get_elm } from "../../tools/elm";
import { next, prev, load_default } from "../../audio/playlist";
import { play, pause, load } from "../../audio/";
import shuffle from "../../tools/shuffle";
import State from "../../audio/state";
import Elms from "../../elements/";

export default function() {
  const elmRoot = Elms.control.root;
  const elmPlay = Elms.control.play;
  const elmLoop = get_elm('mode-repeat', elmRoot);
  const elmShuffle = get_elm('mode-shuffle', elmRoot);
  const elmNext = get_elm('next', elmRoot);
  const elmPrev = get_elm('prev', elmRoot);
  elmPlay.addEventListener('click', function() {
    if (State.control.playing) {
      pause();
    } else {
      play();
    }
  });
  elmLoop.addEventListener('click', function() {
    const isLoop = State.control.loop.list.flag;
    if (isLoop) {
      State.control.loop.list.flag = false;
      this.classList.remove('on');
      this.classList.add('off');
    } else {
      State.control.loop.list.flag = true;
      this.classList.remove('off');
      this.classList.add('on');
    }
  });
  elmShuffle.addEventListener('click', function() {
    const isShuffle = State.playlist.shuffle.length;
    if (isShuffle) {
      State.playlist.shuffle = [];
      this.classList.remove('on');
      this.classList.add('off');
    } else {
      const isPlaying = State.control.playing;
      if (isPlaying) {
        State.playlist.shuffle = shuffle();
        this.classList.remove('off');
        this.classList.add('on');
      } else {
        State.playlist.shuffle = shuffle();
        this.classList.remove('off');
        this.classList.add('on');
        const i = State.playlist.shuffle[0];
        State.playlist.index = i;
        load_default(i);
      }
    }
  });
  elmNext.addEventListener('click', function() {
    const playlist = State.playlist.value;
    const index = next();
    const item = playlist[index];
    load(item);
    State.playlist.index = index;
    play();
  });
  elmPrev.addEventListener('click', function() {
    const playlist = State.playlist.value;
    const index = prev();
    const item = playlist[index];
    load(item);
    State.playlist.index = index;
    play();
  });
}
