import { 
  //wave_build, 
  sync_playart, 
  load_playart
} from "../elements/effect";
import { sync_control } from "../elements/control";
import { sync_meta } from "../elements/meta";
import { sync_playlist } from "../elements/playlist";
import State from "./state";
import subtitle from "./subtitle";
import word from "./word";
import story from "./story";

export const audio = new Audio();

export const context = new AudioContext();

export function load(item) {
  const srcSubtitle = item.subtitle;
  const srcAudio = item.url;
  audio.src = srcAudio;
  audio.load();
  subtitle(srcSubtitle);
  story(item.story);
  word(item.word);
  sync_meta(item);
  //wave_build(srcAudio).then(() => {
  //  console.log('wave build completly');
  //});
}

export function play() {
  //audio.play().then(() => {
  //  sync_playart(true);
  //  sync_control(true);
  //  sync_playlist(true);
  //  State.control.playing = true;
  //});
  audio.play();
  sync_playart(true);
  sync_control(true);
  sync_playlist(true);
  State.control.playing = true;
}

export function pause() {
  const list = State.playlist.value;
  const index = State.playlist.index;
  const item = list[index];
  audio.pause();
  sync_playart(false);
  sync_control(false);
  sync_playlist(false);
  load_playart(item);
  State.control.playing = false;
}



