import { pause, load, audio, play } from "../audio/";
import { next, is_last } from "../audio/playlist";
import State from "../audio/state";

export default function(audio) {
  audio.addEventListener('ended', function() {
    const loopList = State.control.loop.list.flag;
    const loopWhole = State.control.loop.whole.flag;
    const loopPart = State.control.loop.part.flag;
    const playlist = State.playlist.value;
    if (loopPart) return;
    if (loopWhole) {
      audio.currentTime = 0;
      play();
    } else {
      const isLast = is_last();
      if (isLast && !loopList) {
        pause();
      } else {
        const next_index = next();
        const item = playlist[next_index];
        load(item);
        State.playlist.index = next_index;
        play();
      }
    }
  });
}
