import { audio } from "../../audio/";
import State from "../../audio/state";

export function loop_detect() {
  const current = audio.currentTime;
  const loop = State.control.loop;
  const { part } = loop;
  const { flag, start, end } = part;

  if (flag && -1 !== start && -1 !== end) {
    if ((current * 1000) > end) {
      audio.currentTime = start / 1000;
    }
  }
}
