import { get_elm } from "../tools/elm";
import Elms from "./";

export function init_control() {
  Elms.control.root = get_elm('control-container');
  Elms.control.play = get_elm('switch');
  Elms.control.volume.slider = get_elm('volume-slider');
  Elms.control.volume.icon = get_elm('volume-icon');
}

export function sync_control(playing) {
  const elmSwitch = Elms.control.play;
  if (playing) {
    elmSwitch.classList.remove('paused');
    elmSwitch.classList.add('play');
  } else {
    elmSwitch.classList.remove('play');
    elmSwitch.classList.add('paused');
  }
}

export function sync_volume(val) {
  const elmSlider = Elms.control.volume.slider;
  const elmIcon = Elms.control.volume.icon;
  const value = parseFloat(val);
  if (value === 0) {
    elmIcon.classList.add('muted');
  } else {
    elmIcon.classList.remove('muted');
  }
  elmSlider.value = value * 100;
}
