import { get_elm } from "../tools/elm";
import Elms from "./";

export function init_time() {
  Elms.time.current = get_elm('current-time');
  Elms.time.duration = get_elm('duration-time');
}

export function sync_time(current, duration) {
  const elmCurrent = Elms.time.current;
  const elmDuration = Elms.time.duration;

  const [cH, cM, cS] = current;
  const [dH, dM, dS] = duration;

  if ('00' == cH) {
    elmCurrent.innerHTML = `<span>${cM}<span>:<span>${cS}</span>`;
  } else {
    elmCurrent.innerHTML = `<span>${cH}<span>:<span>${cM}</span>:<span>${cS}</span>`;
  }

  if ('00' == dH) {
    elmDuration.innerHTML = `<span>${dM}<span>:<span>${dS}</span>`;
  } else {
    elmDuration.innerHTML = `<span>${dH}<span>:<span>${dM}</span>:<span>${dS}</span>`;
  }
}