import event_subtitle from "../event/elements/subtitle";
import {
  sync_current, 
  build_subtitle
} from "../elements/subtitle";
import State from "./state";

function millisecond(hmsms) {
  const [h, m, s, ms] = hmsms.split(/[:,]/);
  return ((+h*60*60) + (+m*60) + (+s)) * 1000 + (+ms);
}

function extract(lines) {
  const subtitles = [];
  let subtitle = {};

  lines.forEach((line, index) => {
    if (!line.trim().length) return;
    if (Number.isInteger(+line)) {
      subtitle = {};
      subtitle.index = +line;
      return;
    }
    if (!subtitle.startTime) {
      const [startTime, endTime] = line.split(' --> ');
      subtitle.startTime = millisecond(startTime);
      subtitle.endTime = millisecond(endTime);
      return 
    } 

    if (subtitle.text) {
      subtitle.text = (subtitle.text || '') + '<br>' + line;
      return;
    } else {
      subtitle.text = line;
    }
    subtitles.push(subtitle);
  });
  return subtitles;
}

export default async function(url) {
  if (!url) {
    build_subtitle([]);
    State.audio.subtitle.list = [];
    return;
  }
  const res = await fetch(url);
  const text = await res.text();
  const raw_lines = text.split(/\r?\n/);
  const txt_lines = extract(raw_lines);
  build_subtitle(txt_lines);
  State.audio.subtitle.list = txt_lines;
  event_subtitle();
}

export function current(index) {
  if (State.audio.subtitle.current !== index) {
    State.audio.subtitle.current = index;
    sync_current(index);
  }
}

export function reset() {
  State.audio.subtitle.current = -1;
  State.audio.subtitle.list = [];
}

export function recover() {
  State.audio.subtitle.current = -1;
}
