export default function(time) {
  let seconds = Math.floor(time % 60);
  if (seconds < 10) {
    seconds = '0' + seconds;
  }
  let minutes = Math.floor(time / 60);
  if (minutes < 10) {
    minutes = '0' + minutes;
  }
  let hours = '00';
  if (minutes >= 60) {
    hours = Math.floor(minutes / 60);
    minutes = minutes % 60;
  }
  const intHours = parseInt(hours);
  if (intHours > 0 && intHours < 10) {
    hours = '0' + hours;
  }
  return [hours, minutes, seconds];
}
