import State from "../audio/state";
import Elms from "../elements/";

function template_line(o, i) {
  return '' +
  `<div class="line" data-i="${ i }">
    <div class="action">
      <div><div class="play-it"></div></div>
      <div><div class="loop-it"></div></div>
    </div>
    <div class="content">${ o.title }</div>
    <div class="audio-duration">
      <span>${ o.duration }</span>
    </div>
  </div>`;
}

function template_list(template) {
  return `<div id="playlist"><div class="lines">${ template }</div></div>`;
}

export function build_playlist(playlist) {
  const elmList = Elms.content.playlist;
  const template = playlist.map((line, i)=> template_line(line, i)).join('');
  elmList.innerHTML = template_list(template);
}

export function sync_playlist(playing) {
  const elmList = Elms.content.playlist;
  if (!playing) {
    const elmCurrent = elmList.querySelector('.current');
    const elmIcon = elmCurrent.querySelector('.action > div > div');
    elmIcon.classList.remove('playing');
    elmIcon.classList.add('play-it');
    return;
  }

  const elmLines = elmList.querySelectorAll('.line');
  const current = State.playlist.index;
  for (const line of elmLines) {
    const elmPlay = line.querySelector('.action > div > div');
    const elmLoop = line.querySelector('.action .loop-it');
    const index = parseInt(line.dataset.i);
    if (index === current) {
      line.classList.add('current');
      elmPlay.classList.remove('play-it');
      elmPlay.classList.add('playing');
    } else {
      line.classList.remove('current');
      elmPlay.classList.remove('playing');
      elmPlay.classList.add('play-it');
      elmLoop.classList.remove('on');
    }
  }
}
