import Elms from "../elements/";
import { build_wordlist } from "../elements/word";

export default async function(wordParam) {
  const elmWord = Elms.content.word;
  elmWord.innerHTML = '';

  if (!wordParam) return;
  const url = wordParam.path + wordParam.name
  if (!url) return;

  const res = await fetch(url);
  const json = await res.json();
  build_wordlist(json, wordParam);
}
