import { sync_volume } from "../elements/control";
import { audio } from "./";
import State from "./state";

export function init_volume(options) {
  const value = State.control.volume;
  const muted = State.control.muted;

  if (muted) {
    audio.volume = 0;
  } else {
    audio.volume = value;
    sync_volume(value);
  }
}
