import Elms from "../elements/";
import { build_story } from "../elements/story";

export default async function(storyParam) {
  const elmStory = Elms.content.story;
  elmStory.innerHTML = '';

  if (!storyParam) return;
  const url = storyParam.path + storyParam.name
  if (!url) return;

  const res = await fetch(url);
  const text = await res.text();
  build_story(text);
}
