import { sync_area_time } from "../fn/sync";
import { audio } from "../../audio/";
import State from "../../audio/state";
import Elms from "../../elements/";

export default function() {
  const elmTrack = Elms.progress.track;
  elmTrack.addEventListener('input', function() {
    const timeStart = State.audio.duration * (this.value / 100);
    audio.currentTime = timeStart;
    sync_area_time(timeStart);
  });
}
