import { init_progress } from "./progress";
import { init_control } from "./control";
import { init_time } from "./time";
import { init_effect } from "./effect";
import { init_content } from "./content";
import { init_meta } from "./meta";
import Elms from "./";

export default function(options) {
  const rootElm = options.playerWrapperElm;
  const contentElm = options.contentElm;
  const essayElm = options.essayElm;
  Elms.root = rootElm; 
  Elms.content.root = options.contentElm;

  init_progress();
  init_control();
  init_time();
  init_effect();
  init_content();
  init_meta();
}
