import { sync_volume } from "../../elements/control";
import { audio } from "../../audio/";
import State from "../../audio/state";
import Elms from "../../elements/";

export default function() {
  const elmSlider = Elms.control.volume.slider;
  const elmIcon = Elms.control.volume.icon;
  elmSlider.addEventListener('input', function() {
    const value = parseInt(this.value);
    const volume = value / 100;
    if (value === 0) {
      elmIcon.classList.add('muted');
      State.control.muted = true;
    } else {
      elmIcon.classList.remove('muted');
    }
    audio.volume = volume;
    State.control.volume = volume;
  });
  elmIcon.addEventListener('click', function() {
    const muted = State.control.muted;
    if (muted) {
      this.classList.remove('muted');
      State.control.muted = false;
      const volume = State.control.volume;
      if (volume > 0) {
        audio.volume = volume;
      } else {
        audio.volume = 0.5;
        State.control.volume = 0.5;
        sync_volume(0.5);
      }
    } else {
      this.classList.add('muted');
      State.control.muted = true;
      audio.volume = 0;
    }
  });
}
