import { get_elm } from "../tools/elm";
import { context } from "../audio/";
import State from "../audio/state";
import Elms from "./";

function get_peaks(length, buffer) {
  /*
    Set the parameters needed to build the SVG.
  */
  const sampleSize = buffer.length / length;
  const sampleStep = ~~(sampleSize / 10) || 1;
  const numberOfChannels = buffer.numberOfChannels;
  const mergedPeaks = [];

  /*
    Iterate over the channels and find the peaks.
  */
  for (
    let channelNumber = 0;
    channelNumber < numberOfChannels;
    channelNumber++
  ) {
    /*
      Initialize the peaks array and set the channel data to what
      the buffer has in its channel data.
    */
    const peaks = [];
    const channelData = buffer.getChannelData(channelNumber);

    /*
      Iterate over peaks with respect to the sample size.
    */
    for (let peakNumber = 0; peakNumber < length; peakNumber++) {
      /*
        Get the start and end peak.
      */
      const start = ~~(peakNumber * sampleSize);
      const end = ~~(start + sampleSize);

      /*
        Set min and max to the channel data first peak.
      */
      let min = channelData[0];
      let max = channelData[0];

      /*
        Iterate over the parts of the song starting to the
        ending to display the waveform.
      */
      for (
        let sampleIndex = start;
        sampleIndex < end;
        sampleIndex += sampleStep
      ) {
        const value = channelData[sampleIndex];

        if (value > max) {
          max = value;
        }
        if (value < min) {
          min = value;
        }
      }

      /*
        Set the max and min for the peak.
      */
      peaks[2 * peakNumber] = max;
      peaks[2 * peakNumber + 1] = min;

      /*
        Merge the peaks
      */
      if (channelNumber === 0 || max > mergedPeaks[2 * peakNumber]) {
        mergedPeaks[2 * peakNumber] = max;
      }

      if (channelNumber === 0 || min < mergedPeaks[2 * peakNumber + 1]) {
        mergedPeaks[2 * peakNumber + 1] = min;
      }
    }
  }

  /*
    Returns the merged peaks.
  */
  return mergedPeaks;
}

function build_path(peaks) {
  /*
    Get the total peaks in the song.
  */
  const totalPeaks = peaks.length;

  /*
    Figure out the depth of the peak.
  */
  let d = "";
  for (let peakNumber = 0; peakNumber < totalPeaks; peakNumber++) {
    if (peakNumber % 2 === 0) {
      d += ` M${~~(peakNumber / 2)}, ${peaks.shift()}`;
    } else {
      d += ` L${~~(peakNumber / 2)}, ${peaks.shift()}`;
    }
  }

  return d;
}

export function init_effect() {
  Elms.effect.visualizer = get_elm('audio-visualizer');
  Elms.effect.wave = get_elm('wave-form');
  Elms.effect.playart = get_elm('workart-wrapper');
}

export function sync_effect() {
  const elmWave = Elms.effect.wave;
  const sampleRate = State.effect.sample.rate;
  const svg = document.createElementNS('http://www.w3.org/2000/svg', 'svg');
  svg.setAttribute('viewBox', '0 -1 ' + sampleRate + ' 2');
  svg.setAttribute('preserveAspectRatio', 'none');
  const g = document.createElementNS('http://www.w3.org/2000/svg', 'g');
  svg.appendChild(g);
  const path = document.createElementNS(
    'http://www.w3.org/2000/svg',
    'path'
  );
  path.setAttribute('d', '');
  g.appendChild(path);
  elmWave.innerHTML = '';
  elmWave.appendChild(svg);
}

export function wave_build(url) {
  const elmWave = Elms.effect.wave;
  const elmPath = elmWave.querySelector('svg g path');
  elmPath && elmPath.setAttribute('d', '');
  return new Promise((resolve, reject) => {
    const elmWave = Elms.effect.wave;
    const sampleRate = State.effect.sample.rate;
    fetch(url)
    .then(response => response.arrayBuffer())
    .then(arrayBuffer => {
      context.decodeAudioData(arrayBuffer, function(bufferedAudio) {
        const peaks = get_peaks(sampleRate, bufferedAudio);
        const path = build_path(peaks);
        const elmPath = elmWave.querySelector('svg g path');
        elmPath.setAttribute('d', path);
        resolve();
      }); 
    });
  });
  /*
  jsmediatags.read(url, {
    onSuccess: function(tag) {
      console.log(tag.tags);
    }
  });
  */
}

export function sync_playart(playing) {
  const elmPlayart = Elms.effect.playart;
  if (playing) {
    elmPlayart.classList.add('hide');
  } else {
    elmPlayart.classList.remove('hide');
  }
}

export function load_playart(item) {
  const elmPlayart = Elms.effect.playart;
  const elmCover = get_elm('workart', elmPlayart);
  if (!item || !item.artwork) {
    elmCover.style.backgroundImage = '';
    return; 
  }
  elmCover.style.backgroundImage = `url(${ item.artwork })`;
}
