import {
  sync_progress_buffered,
  sync_progress_played
} from "../../elements/progress";
import { sync_time } from "../../elements/time";
import time_format from "../../tools/time";
import { audio } from "../../audio/";
import State from "../../audio/state";

export function sync_area_time(timeCurrent, timeDuration) {
  const current = timeCurrent || audio.currentTime;
  const duration = timeDuration || audio.duration;
  if (isNaN(current) || isNaN(duration)) {
    return;
  }
  State.audio.duration = duration;

  //time elements
  const currentTime = time_format(current);
  const durationTime = time_format(duration);
  sync_time(currentTime, durationTime);

  //progress buffered elements
  const last = audio.buffered.length - 1;
  if (last >= 0) {
    const buffered = audio.buffered.end(last);
    sync_progress_buffered(buffered, duration);
  }
  sync_progress_played(current, duration);
}
